<script>
export default {
  props: {
    date: Date,
    time: String,
    ward: Number,
  },

  data() {
    return {
      count: 20,
      // data: {
      //   date: this.date,

      // }
      things: [ ],
      
    };
  },

  methods: {
    loadData(){
      this.things =  [{ key:"Hello", name:"Thing 1" , value:84 }, { key:"bye", name:"Thing 2" , value:82 },
      { key:"Ahhhhhhhhhhhh", name:"Thing 3" , value:11 },
      { key:"Ahhhhhhhhhhhh", name:"Thing 3" , value:11 },
      { key:"Ahhhhhhhhhhhh", name:"Thing 3" , value:11 },
      { key:"Ahhhhhhhhhhhh", name:"Thing 3" , value:11 },
      { key:"Ahhhhhhhhhhhh", name:"Thing 3" , value:11 },
      { key:"Ahhhhhhhhhhhh", name:"Thing 3" , value:11 }];
    },

    test() {
      alert(
        `The date is ${this.date}, the time is ${this.time} and the chosen ward is ${this.ward}.`
      );
    },
    name() {
      this.count = 5;
    },
    name2() {
      this.count = 10;
    },
  },
};
</script>


<template>
